<template lang="pug">
router-link(:to="href") {{title}}
</template>

<style lang="stylus" scoped>
a
  display: inline-block
  margin-left 0.5rem
  margin-right 0.5rem
</style>

<script>
  import { info } from '@/kailight/utils'
  import { mapGetters } from 'vuex'

  export default {
    name: 'TribefireLink',
    data() {
      return {
      }
    },
    props: {
      debug : Boolean,
      item : {
        type : String,
        default : ''
      },
      id : {
        type : String,
        default : ''
      }
    },
    methods: {
      test() {

      }
    },
    mounted() {
      // info(this.color)
    },
    computed: {
      ...mapGetters(['Users']),
      href() {
        // let BASE = window.location.href.replace(window.location.hash,'')
        // info('link', window.location.href, window.location.hash, BASE)
        let prefix = '/'

        if (this.item == "User") {
          prefix = '/profile/'
          return '/profile/'+this.id
        }

      },
      title() {
        if (this.item == "User") {
          let User
          if ( User = _(this.Users).find( { _id : this.id } ) ) {
            return User.login
          }
          return this.id
        }
        return this.id
      }

    }
  }
</script>



