let BACKEND


import { _, info, extend } from '@/kailight/utils'


let init = (backend) => {
  BACKEND = backend
  let TribefireAuth = {
    state : {
      User : {},
      // contests : [{ id : 1, tasks : [ { id : 1, status: 'complete' }, { id : 2, status: 'pending' } ] }]
      loggedIn : false,
      isAdmin : false,
      isDev : false,
      isPro : false,
    },
    getters : {
      accessLevel : state => {
        let accessLevel = 1
        accessLevel = state.isPro   ? 2 : accessLevel
        accessLevel = state.isAdmin ? 3 : accessLevel
        accessLevel = state.isDev   ? 4 : accessLevel
        return accessLevel
      },
      isDev    : state => state.isDev,
      isAdmin  : state => state.isAdmin,
      isPro    : state => state.isPro,
      User     : state => state.User,
      loggedIn : state => state.loggedIn,
    },
    mutations : {
      setCookie (state,sid) {
        info('mutation setCookie()')
        if (this._vm && this._vm.$cookie) {
          this._vm.$cookie.set('sid', sid, 7);
          info('Set cookie to', sid)
        }
      },
      clearCookie(state,sid) {
        info('mutation clearCookie()')
        if (this._vm && this._vm.$cookie) {
          this._vm.$cookie.delete('sid');
          info('cleared cookie')
        }
      },
      updateAccount: (state,User) => {
        info('mutation updateAccount()')
        let Users = state.Users
        let stateUser = _(state.Users).find( { _id : User._id } )
        if (stateUser) {
          // stateUser = extend(stateUser, User)
          stateUser = User
        }
        state.Users = Users
      },
      deleteUser(state,User) {
        info('mutation deleteUser()')
        let Users = state.Users
        let stateUserKey = _(state.Users).findKey( { _id : User._id } )
        if (stateUserKey) {
          Users.splice(stateUserKey,1)
        }
        state.Users = Users
      },
      updateUser: (state,User) => {
        info('mutation updateUser()')
        if (!User) {
          console.error('No User')
          return
        }
        state.User = extend(state.User, User)
      },
      login: (state,User) => {
        info('login mutation', User)
        if (!User) {
          console.error('No User')
          return
        }
        if (!User._id) {
          console.error('No User._id')
          return
        }
        state.User = User
        state.isAdmin  = (User['tribefire-role'] === 'admin' || User['tribefire-role'] === 'dev')
        state.isDev    = (User['tribefire-role'] === 'dev')
        state.isPro    = (User['tribefire-role'] === 'pro')
        state.loggedIn = true
      },
      logout: (state, User ) => {
        state.loggedIn = false
        state.isAdmin = false
        state.isDev = false
        state.isPro = false
        state.User = User
      },
    },
    actions : {
      updateUser: (context, User) => {
        User = User || context.state.User
        info('action updateUser()', User)

        return new Promise((resolve, reject) => {

          BACKEND.post('accounts/update', User).then( response => {
            info('yeah')
            context.commit('updateAccount', response.data)
            resolve(response.data)
          }).catch(e => {
            info('no')
            console.error(e)
            reject(e)
          })

        })

      },
      deleteUser(context, User) {

        return new Promise( (resolve, reject) => {

          BACKEND.get('accounts/delete/'+User._id ).then( response => {
            info('user deleted')
            context.commit( 'deleteUser', { id : User._id } )
            resolve(response)
          }).catch( e => {
            console.error(e)
            reject(e)
          });
        })

      },
      logout( context ) {
        let url = 'accounts/logout'
        if (context.state.Contest && context.state.Contest._id) {
          url = url+'/'+context.state.Contest._id
        }
        // info('logging out')
        BACKEND.get(url).then( response => {
          // info('logged out')
          this._vm.$cookie.delete('sid')
          context.commit('logout', response.data )
        } ).catch( err => {
          info('ERROR',err)
        })
      },
      loginBySid( context ) {
      info('action loginBySid')

        let sid
        if (this._vm && this._vm.$cookie) {
          sid = this._vm.$cookie.get('sid')
        } else {
          return Promise.reject('No cookie plugin found')
        }

        if (!sid) {
          return Promise.reject('No sid')
        }

        return new Promise( (resolve, reject) => {

          BACKEND.get('accounts/sid/' + sid).then(response => {
            if (response.headers.sid) {
              context.commit('setCookie', response.headers.sid)
            }
            context.commit('login', response.data)
            resolve(response)
          }).catch(err => {
            // console.error('ERROR', err)
            let response = err.response
            if (response && response.status) {
              if (response.status == 403) {
                console.warn('User cookie has expired')
                context.commit('clearCookie')
                reject(response)
                return
              }
            }
            if (response) {
              reject(response)
            } else {
              reject(err)
            }
          })

        })

      },
      autologin(context) {

        return new Promise((resolve, reject) => {
          if (this._vm && this._vm.$cookie) {
            context.dispatch('loginBySid').then(response => {
              resolve(response)
            }).catch(e => {
              info(e)
            })
          }
        })

      },
      login( context, { email, password } ) {

        let login = email
        password = password || context.state.User.password

        if (!login || !password) {
          console.error('No login, no password')
          return
        }
        info('Logging in as ',login,password)

        return new Promise((resolve, reject) => {

          return BACKEND.get('accounts/login/'+login+'/'+password).then(
            response => {
              if (response.headers.sid) {
                context.commit('setCookie', response.headers.sid)
              }
              context.commit('login', response.data)
              resolve(response)
            }
          ).catch( e => {
            let response = e.response
            if (response && response.status) {
              info(response)
              if (response.status == 409) {
                console.warn('Tribefire internal error')
                return reject(response)
              }
            }
            console.error(e)
            reject(e)
          });

        })

      },
      register( context, data ) {

        let regdata = {}
        let ok = false

        if (data.email && data.password) {
          ok = true
          regdata.email = data.email
          regdata.password  = data.password
        } else if (data.login && data.password) {
          ok = true
          regdata.login  = data.login
          regdata.password = data.password
        }

        if (data.role) {
          regdata.role = data.role
        }

        if (!ok) {
          console.error('No login+password or email+password combo')
          return Promise.reject('No login+password or email+password combo')
        }

        info('Registering user', regdata )

        regdata.likesCookies = true
        return new Promise((resolve, reject) => {

          BACKEND.post('accounts/register', regdata).then(
            response => {
              context.commit('login', response.data)
              resolve(response)
            }
          ).catch(
            e => {
              console.error(e)
              if (e.response) {
                reject(e.response)
                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx
                // console.log(error.response.data);
                // console.log(error.response.status);
                // console.log(error.response.headers);
              } else if (e.request) {
                console.log(e.request);
                reject(e.request)
                // The request was made but no response was received
                // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                // http.ClientRequest in node.js
              } else {
                reject(e.request)
                console.log('Error', e.message);
                // Something happened in setting up the request that triggered an Error
              }
              reject(e)
            }
          );

        })

      }
    }
  }
  return TribefireAuth

}

export default init



