let debug = (window && window.location && window.location.hostname === 'localhost') ? true : false
import _ from 'lodash'

let info = debug ? console.info : ()=>{}

let up = function (arr, key, newval) {
  info(arr)
  let match = _(arr).find( { '.key' : key } )
  if (match) {
    let index = _.indexOf(arr, _.find(arr, key))
    arr.splice(index, 1, newval);
  } else {
    info('up: 404')
    arr.push(newval);
  }
};


let extend = function (first,second) {
  return _(second).defaultsDeep(first).value()
  // var a = { a: { y: 1 } };
  // var b = { a: { x: 2, y:2 }, b : { x : 2} };
}

let deepMergeBy = function(a,b,param='id') {
  let result = []
  let merged = []
  _(a).forEach( function(ai,k) {
    if (!ai[param]) {
      return
    }
    let bi = _(b).find( function(bi) { return bi[param] === ai[param] } )
    result.push( extend(ai,bi) )
  })
  return result
}

/*
// deepMergeBy example
let a = [{ id : 1, foo : 2 }, { id : 2, foo : 'bar'}]
let b = [{ id : 2, foo : 2 }, { id : 1, foo : 1}]
info(deepMergeBy(a,b,'id'))
*/

export {
  info, up, _, extend, deepMergeBy
}



