<template lang="pug">
label {{label}}
  input(v-model="_value", :placeholder="placeholder", :type="type")
</template>

<style lang="stylus" scoped>
label
  display block
  margin-top 0.5rem
  font-weight bold
  text-align left
input
  display block
  // border 1px solid #3fc
  border none
  border-radius 4px
  box-shadow 0 2px 3px rgba(0,0,0,.5)
  padding .7rem 1rem
  margin-top 0.5rem
  width: 100%;
&.valid
  color $color-success
  input
    border 1px solid $color-success
&.invalid
  color $color-danger
  input
    border 1px solid $color-danger
</style>

<script>
  import { info } from '@/kailight/utils'

  export default {
    name: 'TribefireInput',
    data() {
      return {
      }
    },
    props: {
      debug : Boolean,
      label : {
        type : String,
        default : ''
      },
      placeholder : {
        type : String,
        default : ''
      },
      type : {
        type : String,
        default : 'text'
      },
      value : {
        type : String,
        default : ''
      }
    },
    methods: {
      updateValue(ev) {
        // info(ev)
        let value = ev.target.value
        this.$emit('input', value)
      }
    },
    mounted() {
      // info(this.color)
    },
    computed: {
      _value: {
        get() {
          return this.value
        },
        set(val) {
          this.$emit('input', val)
          // this.value = val
        }
      }
    }
  }
</script>



