import HelpAdmin from '@/pages/HelpAdmin/HelpAdmin.vue'
import Profile from '@/pages/Profile/Profile.vue'
import Contests from '@/pages/Contests/Contests.vue'
import Events from '@/pages/Events/Events.vue'
import Users from '@/pages/Users/Users.vue'
import Socials from '@/pages/Socials/Socials.vue'

let RoutesAndMenuItems = [
  {
    to : '/admin-help',
    icon : 'question',
    title : 'Help',
    component : HelpAdmin,
    hasMenuItem : true,
    access : 1
  },
  {
    to : '/tribefire-socials',
    icon : 'facebook',
    title : 'Socials',
    component : Socials,
    hasMenuItem : true,
    access : 3
  },
  {
    to : '/profile',
    path : '/profile/:UserId?',
    icon : 'user',
    title : 'Profile',
    component : Profile,
    hasMenuItem : true,
    starting : true
  },
  {
    to : '/contests',
    path : '/contests/:ContestId?',
    icon : 'trophy',
    title : 'Contests',
    component : Contests,
    hasMenuItem : true,
    access : 2
  },
  {
    to : '/events',
    icon : 'trophy',
    title : 'Events',
    component : Events,
    hasMenuItem : true,
    access : 3
  },
  {
    to : '/users',
    icon : 'users',
    title : 'Users',
    component : Users,
    hasMenuItem : true,
    access : 3
  },
  /*
  {
    to : '/client-registration',
    component : ClientRegistration,
  }
  */
  /*
  {
    to : '/dashboard',
    icon : 'trophy',
    title : 'Dashboard',
    disabled : true,
    access : 4
  },
  {
    to : '/games',
    icon : 'gamepad',
    title : 'Games',
    disabled : false,
    access : 2
  }
  */
]

export default RoutesAndMenuItems



