<template lang="pug">
label {{label}}
  select(v-model="_value")
    option(v-for="(option,$index) in _options", :key="$index", :value="option.value") {{option.title}}
</template>

<style lang="stylus" scoped>
label
  display block
  margin-top 0.5rem
  font-weight bold
  text-align left
select
  display block
  // border 1px solid #3fc
  border 1px solid #c36
  padding 0.2rem
  margin-top 0.5rem
  width 100%
  max-width 100%
</style>

<script>
  import { info, extend } from '@/kailight/utils'

  export default {
    name: 'TribefireSelect',
    data() {
      return {
      }
    },
    props: {
      debug : Boolean,
      label : {
        type : String,
        default : ''
      },
      placeholder : {
        type : String,
        default : ''
      },
      value : {
        type : String,
        default : ''
      },
      options : {
        type : Array,
        default : []
      }
    },
    methods: {
      test() {

      },
      updateValue(ev) {
        // info(ev)
        let value = ev.target.value
        this.$emit('input', value)
      }
    },
    mounted() {
      // info(this.color)
    },
    computed: {
      foobar() {
        return 'foobar'
      },
      _value: {
        get() {
          return this.value
        },
        set(val) {
          this.$emit('input', val)
          // this.value = val
        }
      },
      _options() {
        let options = extend( {}, this.options )
        for (let i in options) {
          let option = options[i]
          if (typeof option == 'string') {
            options[i] = { title : option, value : option }
          }
        }
        // info(options)
        return options
      }
    }
  }
</script>



