<template lang="pug" src="./Socials.pug"></template>

<script>

  import { info, _, deepMergeBy } from '@/kailight/utils'
  import { mapGetters } from 'vuex'


export default {
  name: 'Socials',
  components: {
  },
  data () {
    return {
    }
  },
  methods : {
    /*
    loginWithApi(API) {
      info('loginWithApi()',API)
      this.$Socials[API].login()
    },
    */
    saveSocial(Social) {
      info('saveSocial', Social.id)
      this.$store.dispatch('updateSocial', Social).then( response => {
        this.$toaster.success('Social updated')
      }).catch(e => {
        this.$toaster.error('Could not update Social')
      })
    },
    saveApp() {
      this.$store.dispatch('upApp', this.App)
    }
  },
  computed: {
    ...mapGetters(['config','Socials','App','User','debug']),
    socials() {
      let socials = [
        {
          id: 'twitter',
          title: 'Twitter',
          icon : 'brands/twitter',
          ready : true,
          completion : 100,
          props : ['id', 'name', 'screen_name', 'access_token', 'access_token_secret', 'location']
        },
        {
          id: 'instagram',
          ready : true,
          icon : 'brands/instagram',
          title: 'Instagram',
          completion : 100,
          props : ['id', 'access_token', 'bio', 'full_name', 'is_business','username']
        },
        {
          id: 'facebook',
          ready : false,
          title: 'Facebook',
          icon : 'brands/facebook',
          completion : 100,
          props : ['id', 'userId', 'email', 'firstName', 'lastName', 'accessToken']
        },
        {
          id: 'youtube',
          ready : true,
          title: 'Youtube',
          icon : 'brands/youtube',
          completion : 100,
          props : ['id', 'name', 'accessToken','url']
        },
        {
          id: 'twitch',
          ready : true,
          completion : 100,
          title: 'Twitch',
          icon : 'brands/twitch',
          props : ['id', 'name']
        },
        {
          id: 'steam',
          ready : true,
          title: 'Steam',
          completion : 100,
          icon : 'brands/steam',
          props : ['id', 'name', 'realname', 'country', 'home']
        },
        {
          id: 'tribefire',
          ready : true,
          title: 'Tribefire',
          completion : 100,
          icon : 'brands/tribefire',
          props : []
        }]

      for (let social of socials) {
        if (social.id) {
          let Social = _(this.Socials).find( { '.key' : social.id } )
          if (Social) {
            social['.key'] = Social['.key']
          }
        }
      }

      let Socials = this.Socials
      deepMergeBy(Socials,socials)

      socials.sort()
      return socials
    }
  },
  mounted() {
    info('mounted')
    this.$store.dispatch('loadSocials')
  }
}
</script>

<style lang="stylus" scoped>
.block
  margin-left 1rem
  margin-top 3rem
  .block-body
    padding 2rem
  .admin-up
    margin-left: 1rem
    margin-right: 1rem
    margin-top: -3.5rem
    .fa-icon
      padding: 1.7rem
      font-size: 2rem
      color: #fff;
      text-align: left
      margin-right: 1rem
      border-radius: 3px
      box-shadow: 0 2px 9px 0 rgba(0, 0, 0, 0.2), 0 2px 13px 0 rgba(0, 0, 0, 0.2)
    .data
      float: right;
      margin-top: 2rem
      text-align: right
      p
        color: #999999
        font-size: 1.2rem
      p.title
        text-transform: uppercase
        font-weight bolder
        font-size 1.5rem

.classic-admin-card
  .card-body
    color: #fff
    margin-bottom: 0
    padding: 0.9rem
    p
      font-size: 13px
      opacity: 0.7
      margin-bottom: 0
    h4
      margin-top: 10px
</style>

<style lang="stylus" scoped>
.table
  tbody
    th
      font-weight bold
      width 30%
      text-align right

.fa-icon
  background-color #999999
  padding 1rem
  color #fff
  &.twitter
    background-color #4285F4
  &.instagram
    background-color #9933CC
  &.facebook
    background-color #0d47a1
  &.youtube
    background-color #f44
  &.twitch
    background-color #74f

img.tribefire
  background-color #222
  height 5rem
  width 5rem

.debug_hidden
  display none
.debug_expanded
  display block

</style>



