<script>

  import { info, _ } from '@/kailight/utils'
  import { mapGetters } from 'vuex'

  export default {
    name: 'HelpAdmin',
    components: {
    },
    data () {
      return {
      }
    },
    methods : {
      setControls(controls = null) {
        let Component = this
        let defaultControls = [
          {
            title: 'Example Control main',
            type: 'main',
            action: () => { Component.stub(true) }
          },
          {
            title: 'Example Control alt',
            type: 'alt',
            action: () => { Component.stub(false) }
          }
        ]
        controls = controls || defaultControls
        this.$store.commit('setControls', controls)
      }
    },
    computed: {
      ...mapGetters(['config', 'isAdmin']),
    },
    mounted() {
      this.setControls()
    }
  }
</script>



<template lang="pug">
.page#help-admin
  section
    tf-block
      tf-block-header Debugging
      tf-block-body
        p Too much spam in console (or not enough)
        p By default if Admin Panel is on localhost the debug is enabled, and spams too much info
        p While on non-localhost it is totally disabled
        p You can change that by editing top lines in
          b /src/kailight/utils/kailight-utils.js

  section
    tf-block
      tf-block-header Controls (on bottom)
      tf-block-body
        p You can set bottom controls by calling
        code this.$store.commit('setControls', controls)
        p Example controls
        code
          | let Component = this
          | let controls = [
          |   {
          |     title  : 'Update',
          |     type   : 'main',
          |     action : Component.saveCurrent
          |   },
          |   {
          |     title : 'Cancel',
          |     type  : 'alt',
          |     action : Component.cancelCurrent,
          |   }
          | ]
          | this.$store.commit('setControls', controls)

        p For all pages we wrap it ino component method .setControls() and call (with the defaults) on page mount

  section
    tf-block
      tf-block-header Database
      tf-block-body
        p The database has moved to backend, somewhere around&nbsp;
          a(target="_blank" href="http://tribefire-auth.herokuapp.com/tribefire-info") tribefire-auth.herokuapp.com
        p You can only edit where the app points to, localhost or live

        h5 How to switch backend/database, from localhost to heroku
        p in store.js
        code
          | let BASE = 'http://localhost:3000/'
          | // to
          | let BASE = 'http://tribefire-auth.herokuapp.com/'

  section
    tf-block
      tf-block-header Pages
      tf-block-body
        h4 How to create a new page/route, or add an item into menu
        p config/routes-and-menuitems.js has the data for both routes and menu items
        code
          | {
          |   to : '/admin-help',
          |   icon : 'question',
          |   title : 'Help',
          |   component : HelpAdmin,
          |   hasMenuItem : true,
          |   access : 1
          | },
        span you can simply copypase with custom icon/to/title/<b>component</b>:
        br

        h4 Access levels
        p 1 - non-client, 2 - client, 3 - admin, 4 - dev (debug on)

        h4 How to set permissions for page access or specific part of page
        p Simply use v-if="isAdmin" condition on html element
        p 1. Add isAdmin by mapping getters from vuex:
        code
          | import { mapGetters } from 'vuex'
        p 2. In component .js
        code
          | computed: {
          | ...mapGetters(['isAdmin'])

  section
    tf-block
      tf-block-header TribeFire Components
      tf-block-body
        p Set of components for TF admin, all located under src/Tribefire/
        br
        h5 tf-wrapper
        p Supposed to be used along with #app root element, e.g.
        code &lt;tf-wrapper id="app"&gt;...all the html...&lt;/tf-wrapper&gt;
        p Adds .landscape or .portrait depending on screen orientation
        p Also adds some classes sm/md/lg depending on screen width in case of landscape or screen height in case of portrait
        p Example usage in css:
        code
          | #app.portrait .myelement {
          |   display: none
          | }
          |
          | #app.landscrape .myelement {
          |   display: auto
          | }

        h5 tf-diamonds
        p shows diamonds icon + user calculated diamonds if empty
        h5 tf-input
        p input wrapped for styling
        h5 tf-select
        p select wrapped for styling
        p pass options as :options="options" where options are [{ title "Option text", value : "1" } ]
        h5 tf-block + tf-block-header + tf-block-body
        p Blocks for styling
        p Have to set their display manually for various screens

  section
    tf-block
      tf-block-header Something else
      tf-block-body
        | Nothing here yet

</template>


<style lang="stylus" scoped>

</style>



