import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { info } from '@/kailight/utils'

require('normalize.css')


let VueCookie = require('vue-cookie');
Vue.use(VueCookie);


import VModal from 'vue-js-modal'
import '@/modal.styl'
Vue.use(VModal)

import VTooltip from 'v-tooltip'
import '@/tooltip.styl'
Vue.use(VTooltip)

// Toaster
import Toaster from 'v-toaster'
import '@/toaster.styl'
Vue.use(Toaster, { timeout: 2500 } )


Vue.component('icon', Icon)



Vue.config.productionTip = false

// adds stub() to any component
Vue.mixin({
  methods: {
    stub: function (success = false) {
      info(success)
      this.$store.dispatch('stub', success).then(data => {
        this.$toaster.success('stub success')
      }).catch(e => {
        this.$toaster.error('stub error')
      })
    }
  }
})

import debug from '@/components/Debug/Debug.vue'
Vue.component('debug', debug)



import tf_row from '@/Tribefire/Row.vue'
Vue.component('row', tf_row)

import tf_wrapper from '@/Tribefire/Wrapper.vue'
Vue.component('tf-wrapper', tf_wrapper)

import tf_navbar from '@/Tribefire/Navbar.vue'
Vue.component('tf-navbar', tf_navbar)

import tf_controls from '@/Tribefire/Controls.vue'
Vue.component('tf-controls', tf_controls)

import tf_input from '@/Tribefire/Input.vue'
Vue.component('tf-input', tf_input)

import tf_select from '@/Tribefire/Select.vue'
Vue.component('tf-select', tf_select)

import tf_login_signup from '@/Tribefire/LoginSignup.vue'
Vue.component('tf-login-signup', tf_login_signup)

import tf_button from '@/Tribefire/Button.vue'
Vue.component('tf-button', tf_button)

import tf_link from '@/Tribefire/Link.vue'
Vue.component('tf-link', tf_link)

import tf_diamonds from '@/Tribefire/Diamonds.vue'
Vue.component('tf-diamonds', tf_diamonds)

import tf_block from '@/Tribefire/Block.vue'
import tf_block_header from '@/Tribefire/BlockHeader.vue'
import tf_block_body from '@/Tribefire/BlockBody.vue'

import Icon from '@/Tribefire/Icon'
Vue.component(Icon)

Vue.component('tf-block', tf_block )
Vue.component('tf-block-header', tf_block_header )
Vue.component('tf-block-body', tf_block_body )

new Vue({
  router,
  store,
  render: h => h(App),
  mounted() {
    store.dispatch('autologin').then( data => {
      // info('autologin callback', data)
    }).catch(e => {
      console.warn('Some error happened on autologin')
    })
  }
}).$mount('#app')



