<script>
import GAMES from '@/config/games'
import { info } from '@/kailight/utils'
// info = ()=>{}

// info('GAMES',GAMES)

export default {
  name: 'Games',
  props: {
    value: {
      type: Array,
      default: () => {
        return []
      }
    },
  },
  components: {
  },
  data() {
    return {
      filteredGames : [],
      gameFilter : '',
      favoriteGames : [],
    }
  },
  methods: {
    applyGameFilter() {
      let f = this.gameFilter.toLocaleLowerCase()
      info('gameFilter', this.gameFilter, f.length)
      if (f.length > 0 && f.length < 4) {
        this.filteredGames = _(this.allGames).filter((g) => {
          return g.toLocaleLowerCase().indexOf(f) === 0
        }).value()
      }
      else if (f.length > 3) {
        this.filteredGames = _(this.allGames).filter((g) => {
          return g.toLocaleLowerCase().indexOf(f) > -1
        }).value()
      }
      else {
        this.filteredGames = []
      }
      info('filteredGames', this.filteredGames)
    },
    addToFavorites(game) {
      this.favoriteGames.push(game)
      // this.$emit('updated', this.favoriteGames)
      this.onValueChanged( this.favoriteGames )
    },
    removeFromFavorites(game) {
      let k = this.favoriteGames.indexOf( game )
      this.favoriteGames.splice(k, 1)
      this.onValueChanged( this.favoriteGames )
    },
    onValueChanged(val) {
      this.$emit('input', val)
    }
  },
  computed: {
    allGames() {
      let allGames = _(GAMES).map('title').uniq().value();
      return allGames
    }
  },
  created() {
    // let games = this.value
    // info(games)
    // this.text = activeOpt.title
  },
  mounted() {
    // info('TribefireInputGames mounted!')
    // info(this.value)
    let games = []
    // info('?',this.value,this.allGames.slice(0,2))

    for (let gameTitle of this.value) {
      // info(gameTitle)
      games.push(gameTitle)
    }
    this.favoriteGames = games
  }
}
</script>


<template lang="pug">
div.gameSearch
  form(@submit.prevent="applyGameFilter", style="margin-top:0;")
    label(for="UserFavoriteGames") What are your favourite games?
    input#UserFavoriteGames(placeholder="You can select multiple..." v-model="gameFilter", @keyup="applyGameFilter", @change="applyGameFilter")
    div.suggestions(v-if="filteredGames.length")
      div.resultsDiv(style="margin-bottom:1rem;")
        span.searchResult(v-for="game in filteredGames.slice(0,15)", @click="addToFavorites(game)") {{game}} <b style="color: #0c0">+</b>
    div.favorites(v-if="favoriteGames.length")
      div(style="font-weight: bold;") Your favourites
      div.resultsDiv
        span.searchResult(v-for="game in favoriteGames", @click="removeFromFavorites(game)") <b style="color: red;">-</b> {{game}}
</template>


<style lang="stylus" scoped>

.gameSearch
  width 300px

.searchResult
  padding .3rem .5rem
  margin-top .3rem
  margin-right .5rem
  margin-bottom .3rem
  border-radius 0.1rem
  box-shadow 0 0 5px rgba(0,0,0,.5)
  cursor: pointer

.resultsDiv
  margin-top .5rem
  display flex
  flex-wrap wrap

.favorites
  margin-top .5rem

input
  padding .8rem 1rem
  width 90%
  margin-top 0.7rem
  border none
  border-radius 2px
  background white
  box-shadow 0 0px 4px rgba(0,0,0,.2)
  outline none

label
  display block
  margin-top 0.5rem
  font-weight bold
</style>



