<script>

  import { info, _ } from '@/kailight/utils'
  import { mapGetters } from 'vuex'
  import moment from 'moment'

  export default {
    name: 'TribefireEventsPage',
    components: {
    },
    data () {
      return {
        entityFilter : null,
        actionFilter : null,
        itemIdFilter : null,
        refreshInterval : null
      }
    },
    methods : {
      applyFilters() {
        info('Applying filters')
        // this.$toaster.success('Events filtered')
        // this.$toaster.warning('This will apply filters')
      },
      clearFilters() {
        this.entityFilter = null
        this.actionFilter = null
        this.itemIdFilter = null
      },
      ago(timeStr) {
        return moment().to(new Date(parseInt(timeStr)) )
      },
      setControls(controls = null) {
        let Component = this
        let defaultControls = [
          {
            title: 'Clear Filters',
            type: 'default',
            action: () => { Component.clearFilters() }
          },
          {
            title: 'Check for new events',
            type: 'success',
            action: () => { Component.manualUpdate(false) }
          }
        ]
        controls = controls || defaultControls
        this.$store.commit('setControls', controls)
      },
      manualUpdate() {
      info('manualUpdate()')
        this.updateEvents()
      },
      refresh() {
        info('refresh')
        this.$forceUpdate()
      },
      updateEvents() {
        this.$store.dispatch('checkEvents').then( data => {
          if (data.length) {
            console.info('Got ', data.length, ' new events')
          } else {
            this.$toaster.warning('Nothing new')
            console.info('No new events')
          }
        }).catch(e => {
          info(e)
          console.warn('Error while checking for events')
        })
      }
    },
    computed: {
      ...mapGetters(['config', 'isAdmin', 'Events']),
      filters() {
        let filters = {}
        if (this.entityFilter)
          filters.entity = this.entityFilter
        if (this.actionFilter)
          filters.action = this.actionFilter
        if (this.itemIdFilter)
          filters.itemId = this.itemIdFilter

        return filters
      },
      events() {
        // info('entity', this.filters.entity )
        let filters = this.filters

        let events = _(this.Events).reverse().filter( filters ).value()
        info('Filtered out ', events.length, 'events out of ', this.Events.length)
        return events
      },
    },
    mounted() {
      this.setControls()
      this.updateEvents()
      this.refreshInterval = setInterval(this.refresh, 5000)
    },
    beforeDestroy() {
      clearTimeout(this.refreshInterval)
    },
    created() {
      info('Events page created')
    }
  }
</script>



<template lang="pug">
.page#events
  section
    tf-block
      tf-block-header Event filters
      tf-block-body
        row
          div
            tf-select(v-model="entityFilter", :options="[ 'User', 'Something Else' ]" label="Entity")
          div
            tf-select(v-model="actionFilter", :options="[ 'login', 'update' ]" label="Action")
          div
            tf-select(v-model="itemIdFilter", :options="[ '' ]", :label="entityFilter+' Id'")

  section
    tf-block
      tf-block-header Event log
      tf-block-body
        // code {{events}}
        row(v-for="(event,$index) in events", :key="$index")
          div() {{ago(event.time)}}
          div(v-if="event.entity=='User' && event.action=='login'") User
            tf-link(item="User", :id="event.params.User")
            | logged in.
          div(v-if="event.entity=='User' && event.action=='update'") User
            tf-link(item="User", :id="event.params.User")
            | profile updated.

        // table.event(style="width 100%")
          thead
            tr()
              th Who
              th What
              th When
              th Extra info
          tbody
            tr(v-for="event in Events")
              td {{ago(event.time)}}
              td {{event.entity}}
              td {{event.action}}
              td {{event.params._id}}

</template>


<style lang="stylus" scoped>

</style>



