<script>
import { info } from '@/kailight/utils'
import RoutesAndMenuItemsConfig from '@/config/routes-and-menuitems'
import { mapGetters } from 'vuex'

export default {
  name: 'TribefireNavbar',
  components: {
  },
  data() {
    return {
      activeMenuItem : {}
    }
  },
  methods: {
    saveCurrent() {
      this.stub(true)
      info('saving current')
    },
    cancelCurrent() {
      this.stub(false)
    },
    isActive(item={}) {
      return this.activeMenuItem == item
    },
    setActive(item) {
      this.activeMenuItem = item
    },
    toggleDebug() {
      info('yes')
      this.$store.commit('toggleDebug')
    }
  },
  computed: {
    ...mapGetters(['isDev','isAdmin','isPro','accessLevel']),
    menuItems() {

      let accessLevel = this.accessLevel

      info('USER ACCESS LEVEL', accessLevel)

      let menuItems = _(RoutesAndMenuItemsConfig).filter({ 'hasMenuItem' : true }).value()

      return menuItems.filter( (i) => { return !i.disabled && (!i.access || accessLevel >= i.access) })
    }
  },
  mounted() {
    info('Navbar: mounted')
    let activeRoute = this.$route.matched[0]
    let activeMenuItem

    if (!activeRoute) {
      activeMenuItem = _(this.menuItems).find( { starting : true } )
      this.setActive( activeMenuItem )
      return
    }

    // info(activeRoute)
    activeMenuItem = _(this.menuItems).find( { to : activeRoute.path } )
    this.setActive( activeMenuItem )
  }
}
</script>

<template lang="pug">
#nav
  .logoDiv
    img#logo(src="@/assets/logo.png" width="260" height="46")
  .wrapper
    router-link.menuItem(v-for="(menuItem,k) in menuItems", :to="menuItem.to", @click.native='setActive(menuItem)', :key="k")
      span(:class="{selectedRoute: activeMenuItem.to == menuItem.to}") {{menuItem.title}}
</template>


<style lang="stylus">
#nav
  background $color-purple
  display flex
  flex-direction column
  justify-content center
  align-items center
  position auto
  height auto
  flex-grow 1
  margin-bottom 1rem
  width 100vw
  .wrapper
    background white
    width 100%
    height 100%
    display flex
    flex-direction row
    justify-content center
    box-shadow 0 2px 4px rgba(0,0,0,.2)
    .menuItem
      color #16161d
      display flex
      justify-content center
      align-items center
      text-decoration none
      background transparent
      span
        padding .8rem 2rem
  .logoDiv
    display flex
    justify-content center
    align-items flex-start
    width 100%
  #logo
    margin 0
    padding 1.5rem 2.2rem
  #content
    padding: 3rem 1rem 5rem 1rem;
    margin: 1rem auto
    width: 90%
  .selectedRoute
    border-bottom 4px solid $color-purple!important
    color $color-purple
    background #fefefe
    // box-shadow 0 0px 2px rgba(0,0,0,.2)

</style>



