import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import { _, info, extend } from '@/kailight/utils'

import BASE from './BASE'

import axios from 'axios'
const BACKEND = axios.create({
  baseURL : BASE,
  withCredentials: true,
})


import tribefireAuthInit from '@/kailight/tribefire-auth'
let TribefireAuth = tribefireAuthInit(BACKEND)


const store = new Vuex.Store({
  state: {
    ...TribefireAuth.state,
    BASE : null,
    debug: true,
    loaded: false,
    backendNotFound: false,
    controls : [],
    Contests : [],
    Socials : [],
    Contest: null,
    Entries : [],
    Events : [],
    Users : [],
    UserHistory : []
  },
  mutations: {
    ...TribefireAuth.mutations,
    onSocialLogin: (state, data) => {
      info('mutation onSocialLogin', data)
      if (data.social) {
        state.User[data.social] = data.data
        state.User[data.social].connected = true
      }
    },
    pickContestWinner( state, { cid, winner } ) {
    info('mutation pickContestWinner', cid, winner)
      let Contest = _(state.Contests).find( { _id : cid } )
      Contest.winner = winner
      // state.Contest.winner = winner
    },
    setUserHistory( state, UserHistory ) {
      state.UserHistory = UserHistory
    },
    resetUserHistory( state, UserHistory ) {
      state.UserHistory = UserHistory
    },
    addUser(state,User) {
      state.Users.push(User)
    },
    setControls(state, data) {

      if (!Array.isArray(data)) {
        console.error('Bad controls')
        return
      }

      for ( let item of data ) {
        if (!item.type) {
          console.warn('Control should have type')
        }
        if (!item.title) {
          console.warn('Control should have title')
        }
        if (!item.action) {
          console.warn('Control should have action')
        }
      }
      state.controls = data

    },
    toggleDebug: (state) => {
      state.debug = !state.debug
    },
    loaded: (state, val = false) => {
      state.loaded = val
    },
    backendNotFound: (state, val = false) => {
      state.backendNotFound = val
    },
    setContest: (state, data) => {
      if (data) {
        state.Contest = data
      }
    },
    addContest(state,data) {
      if (data) {
        state.Contests.push(data)
      }
    },
    addEvents: (state, events) => {
      for (let event of events) {
        state.Events.push(event)
      }
    },
    setEntries: (state, { id, data } ) => {
      // id is UserId
      state.Entries = data
    },
    setUsers: (state, data ) => {
      state.Users = data
    },
    setBASE: (state, val=null) => {
      let BASE = val
      if (BASE && BASE.charAt(BASE.length-1) !== '/') {
        BASE = BASE+'/'
      }
      state.BASE = BASE
    },
    setContests: (state, contests) => {
      info('setContests', contests)
      state.Contests = contests
    },
    setSocials: (state, socials) => {
      info('setSocials',socials)

      state.Socials = socials
    },
    setUserContest: (state, contestId) => {

      if (state.User) {
        if (!state.User.contests)
          state.User.contests = {}
        let contestIndex = _(state.User.contests).findIndex({_id: contestId})
        if (contestIndex === -1) {
          state.User.contests[contestId] = {_id: contestId, tasks: []}
        }
      }
      info('state.User.contests', state.User.contests)

    },
  },
  getters: {
    ...TribefireAuth.getters,
    Users: state => state.Users,
    Socials: state => state.Socials,
    controls: state => state.controls,
    BASE : state => state.BASE,
    Entries : state => state.Entries,
    UserHistory : state => state.UserHistory,
    /*
    UserTasks: state => {
      let uc
      let tasks = []
      if (state.User && state.User.contests && this.state.Contest) {
        if (uc = _(state.User.contests).find( { _id: this.Contest.id } )) {
          tasks = uc.tasks
        }
        return tasks
      }

    },
    */
    Contest: state => {
      let Contest = state.Contest
      if (Contest && Contest.tasks) {
        for (let task of Contest.tasks) {
          if (task.social === 'twitter') task.icon = 'brands/twitter'
          if (task.social === 'facebook') task.icon = 'brands/facebook'
          if (task.social === 'instagram') task.icon = 'brands/instagram'
          if (task.social === 'twitch') task.icon = 'brands/twitch'
          if (task.social === 'steam') task.icon = 'brands/steam'
          if (task.social === 'youtube') task.icon = 'brands/youtube'
        }
      }
      return Contest
    },
    Contests: state => state.Contests,
    Events: state => state.Events,
    debug: state => {
      return state.debug && state.isDev
    },
    loaded: state => state.loaded,
    backendNotFound: state => state.backendNotFound
  },
  actions: {
    ...TribefireAuth.actions,
    onSocialLogin: (context, data) => {
      info('action onSocialLogin()', data)
      context.commit('onSocialLogin', data)
      /*
      if (data.social == 'Facebook') {
        context.dispatch('updateUser', context.state.User)
      }
      */
    },
    pickContestWinner(context, contest) {
    info('action pickContestWinner()', contest._id)

      if (!contest._id) {
        return new Promise.reject('The Contest has no _id')
      }

      return new Promise( (resolve, reject) => {

        return BACKEND.get('contests/'+contest._id+'/roll').then( response => {
          info(response.data)
          context.commit( 'pickContestWinner', { cid : contest._id, winner : response.data } )
          resolve(response.data)
        }).catch( e => {
          console.error(e)
          reject(e)
        });
      })


    },
    loadUsers(context) {
    info('action loadUsers()')

      return new Promise( (resolve, reject) => {

        return BACKEND.get('accounts').then( response => {
          context.commit('setUsers', response.data)
          resolve(response.data)
        }).catch( e => {
          console.error(e)
          reject(e)
        });
      })

    },
    loadUser(context,UserId) {
    info('action loadUser()' )

      if (!UserId) {
        return Promise.reject('store.loadUser(): No UserId')
      }

      return new Promise( (resolve, reject) => {

        return BACKEND.get('accounts/'+UserId).then( response => {
          context.commit('addUser', response.data)
          resolve(response.data)
        }).catch( e => {
          console.error(e)
          reject(e)
        });
      })

    },
    loadUserHistory(context,Profile) {
      info('action loadUserHistory()' )

      if (!Profile) {
        return Promise.reject('store.loadUserHistory(): No Profile')
      }
      if (!Profile._id) {
        return Promise.reject('store.loadUserHistory(): No Profile._id')
      }

      return new Promise( (resolve, reject) => {

        return BACKEND.get('events/user/'+Profile._id).then( response => {
          context.commit('setUserHistory', response.data)
          resolve(response.data)
        }).catch( e => {
          console.error(e)
          reject(e)
        });

      })

    },
    createContest(context,data) {
    info('action createContest()' )

      return new Promise( (resolve, reject) => {
        BACKEND.post('contests/create').then( response => {
          if (response.data) {
            context.commit('addContest', response.data)
            context.commit('setContest', response.data)
          }
          resolve(response.data)
        }).catch( e => {
          console.error(e)
          reject(e)
        });
      })

    },
    checkEvents(context,data) {
    info('action checkEvents()' )

      return new Promise( (resolve, reject) => {
        return BACKEND.get('events').then( response => {
          if (response.data.length) {
            context.commit('addEvents', response.data)
          }
          resolve(response.data)
        }).catch( e => {
          console.error(e)
          reject(e)
        });
      })
    },
    loadUserContests: (context) => {

      info('action loadUserContests()' )

      return new Promise( (resolve, reject) => {

        return BACKEND.get('contests/my').then(
          response => {
            context.commit('setContests', response.data)
            resolve(response)
          }
        ).catch(
          e => {
            console.error(e)
            reject(e)
          }
        );
      })

    },
    loadSocials: (context) => {

      info('action loadSocials()' )

      return new Promise( (resolve, reject) => {

        return BACKEND.get('socials/all').then(
          response => {
            info('yay')
            context.commit('setSocials', response.data)
            resolve(response)
          }
        ).catch(
          e => {
            console.error(e)
            reject(e)
          }
        );
      })

    },
    updateSocial: (context, Social) => {
      info('action updateSocial()')

      return new Promise( (resolve, reject) => {

        return BACKEND.post('socials/update', Social).then(
          response => {
            resolve(response)
          }
        ).catch(
          e => {
            console.error(e)
            reject(e)
          }
        )
      })

    },
    updateContest: (context, Contest) => {

      info('updateContest()', Contest )

      return new Promise( (resolve, reject) => {

        return BACKEND.post('contests/update', Contest ).then(
          response => {
            resolve(response)
          }
        ).catch(
          e => {
            console.error(e)
            reject(e)
          }
        );

      })
    },
    stub: (context,data) => {
    info('stub()', data )

      return new Promise( (resolve, reject) => {

        if (data === false) {
          reject(data)
        }
        resolve(data)

      })
    },
    getUserEntries: (context, id) => {
    info('getUserEntries()')

      return new Promise( (resolve, reject) => {

        let url = 'accounts/entries'
        if (id) url += '/'+id

        info(url)

        return BACKEND.get(url).then( response => {
          context.commit( 'setEntries', { id : id, data : response.data } )
          resolve(response)
        }).catch( e => {
          console.error(e)
          reject(e)
        })

      })

    },

  }

})


store.commit('setBASE', BASE)


export default store



