<template lang="pug">
div.tribefire-diamonds
  img(:src="'/static/diamonds.svg'")
  b {{diamonds}}
</template>

<style lang="stylus" scoped>
.tribefire-diamonds
  display flex
  align-items center
  img
    height:25px; margin-left:5px;
  b
    display inline-block
    margin-left .5rem
    font-family "Bungee"
</style>

<script>
/*
import {
  Btn, waves
} from '@/libs/mbv'
*/

import { info } from '@/kailight/utils'
import { mapGetters } from 'vuex'

export default {
  name: 'TribefireDiamonds',
  methods: {
  },
  computed: {
    ...mapGetters(['User']),
    diamonds() {
      if (this.defaultDiamonds) {
        return this.defaultDiamonds
      }
      return this.User.diamonds || 0
    },
    defaultDiamonds() {
      if (!this.$slots || !this.$slots.default || !this.$slots.default[0]) {
        return this.User.diamonds
      }
      return this.$slots.default[0].text || this.User.diamonds
    }
  },
  mounted() {
    info('default diamonds', this.$slots.default)
    // info('diamonds!!')
  }
}
</script>



