<script>

  import { info, _ } from '@/kailight/utils'
  import { mapGetters } from 'vuex'
  import moment from 'moment'

  export default {
    name: 'TribefireUsersPage',
    components: {
    },
    data () {
      return {
        loginFilter : null,
      }
    },
    methods : {
      applyFilters() {
        info('Applying filters')
        // this.$toaster.success('Events filtered')
        // this.$toaster.warning('This will apply filters')
      },
      clearFilters() {
        this.loginFilter = null
      },
      reloadUsers() {

        this.$store.dispatch('loadUsers').then( data => {
          if (data.length) {
            console.info('Got ', data.length, ' users')
          }
        }).catch(e => {
          info(e)
          this.$toaster.error('Error while loading')
        })

      },
      setControls(controls = null) {
        let Component = this
        let defaultControls = [
          {
            title: 'Reload',
            type: 'main',
            action: () => { Component.reloadUsers() }
          },
        ]
        controls = controls || defaultControls
        this.$store.commit('setControls', controls)
      }
    },
    computed: {
      ...mapGetters(['config', 'isAdmin', 'Users']),
      filters() {
        let filters = {}
        if (this.loginFilter)
          filters.login = this.loginFilter

        return filters
      },
      users() {
        // info('entity', this.filters.entity )
        let filters = this.filters

        let users = _(this.Users).filter( filters ).value()
        info('Filtered out ', users.length, 'users out of ', this.Users.length)
        return users
      },
    },
    mounted() {
      info('Users page mounted')
      this.setControls()
      this.reloadUsers()
    },
    beforeDestroy() {

    },
    created() {

    }
  }
</script>



<template lang="pug">
.page#events
  section
    tf-block
      tf-block-header User filters
      tf-block-body
        row
          div
            tf-input(v-model="loginFilter", :options="[ 'User', 'Something Else' ]" label="Login")
        div Login filters don't work atm, <b>gotta decide if we filter on server or on client</b>

  section
    tf-block
      tf-block-header Users
      tf-block-body

        table(style="width: 100%")
          thead
            tr
              th Id
              th Login
              th Role
          tbody
            // code {{events}}
            tr(v-for="(user,$index) in users", :key="$index")
              td()
                tf-link(item="User", :id="user._id")
              td() {{user.login}}
              td() {{user['tribefire-role']}}

</template>


<style lang="stylus" scoped>

</style>



