<template lang="pug">
#tribefire-navbar-controls
  .left
    tf-button.float-left(type="danger", @click.native="logout") Logout
  .right
    tf-button.float-right(v-for="(control,$index) in controls", :type="control.type", @click.native="control.action()", :key="$index") {{control.title}}
  // tf-button.float-right(color="alt", @click.prevent="cancelCurrent") Cancel
</template>

<style lang="stylus" scoped>
.float-right
  float right
#tribefire-navbar-controls
  position fixed
  display flex
  justify-content space-between
  align-items center
  height 4rem
  bottom 0
  right 0
  padding-right 1rem
  width 100%
  background $bg-color-alt
  box-shadow 0 -1px 5px rgba(0,0,0,.2)
  line-height 3rem


.left
  margin-left 2rem
.right
  margin-right 0
</style>

<script>
import { info } from '@/kailight/utils'
import { mapGetters } from 'vuex'

export default {
  name: 'TribefireControls',
  components: {
  },
  methods: {
    saveCurrent() {
      this.stub(true)
      info('saving current')
    },
    cancelCurrent() {
      this.stub(false)
    },
    logout() {
      this.$store.dispatch('logout')
    }
  },
  computed: {
    ...mapGetters(['controls'])
  }
}
</script>



