import Vue from 'vue'
import Router from 'vue-router'

import { info } from '@/kailight/utils'

Vue.use(Router)

import RoutesConfig from '@/config/routes-and-menuitems'


let routes = []
for (let route of RoutesConfig) {
  if (!route.to && route.path) route.to = route.path
  if (!route.path && route.to) route.path = route.to
  // info(route)
  routes.push({
    path : route.path,
    component : route.component
  })
}

info('routes', routes)

export default new Router({
  routes
})



