<script>
import { mapGetters } from 'vuex'

import { info, extend } from '@/kailight/utils'

import options from '@/config/options'

import moment from 'moment'

// https://github.com/charliekassel/vuejs-datepicker#install
import Datepicker from 'vuejs-datepicker';

// this options didn't work
// https://www.npmjs.com/package/vue-material-datepicker

export default {
  name: 'Contests',
  components: {
    'tf-datepicker': Datepicker
  },
  filters: {
    formatDate: function (value) {
      if (!value) return ''
      value = value.toString()
      let d = new Date()
      d.setTime(value)
      return moment(d).format('Do MMMM');
    }
  },
  data() {
    return {
      Contest: null,
      creating: false,
      newTask : {
        social : null,
        title : null,
      },
      Task : null,
      TasksConfig : {
        twitter : {
          types: {
            follow: {
              validator: /.+twitter.com\/([^\/.]+)/,
              match : 1,
              prop: ['page'],
              hint: 'Paste Twitter page url here, example: <b>https://twitter.com/MyPage</b>'
            },
            retweet: {
              validator: /.+twitter.com\/(.+?)\/status\/([0-9]+)/,
              match : 1,
              prop: ['post'],
              hint: 'Paste Twitter post url here, example: <b>https://twitter.com/MyPage/status/2132134</b>'
            }
          }
        },
        facebook : {
          types : {
            like : {
              validator: /.+facebook.com\/([^\/.]+)/,
              match : 1,
              hint: 'Paste Facebook page url here, example: <b>https://facebook.com/MyPage</b>'
            },
            repost : {
              validator: /.+facebook.com\/.+?\/posts\/([^\/.]+)/,
              match : 1,
              hint: 'Paste Facebook post url here, example: <b>https://facebook.com/MyPage/posts/376881606134835</b>'
            }
          }
        },
        instagram : {
          types : {
            follow : {
              validator: /.+instagram.com\/([^\/.]+)/,
              match : 1,
              hint: 'Paste Instagram page url here, example: <b>https://instagram.com/MyPage</b>'
            },
            'like and friend' : {
              validator: /.+instagram.com\/p\/([^\/.]+)/,
              match : 1,
              hint : 'Paste Instagram post url here, example: <b>https://instagram.com/p/BiXeOw8lyie</b>'
            },
          },
        },
        youtube : {
          types : {
            subscribe : {
              validator: /.+youtube.com\/([^\/.]+)/,
              match : 1,
              hint: 'Paste YouTube channel url here, example: <b>https://youtube.com/MyChannel</b>'
            },
          },
        },
        twitch : {
          types : {
            follow : {
              validator: /.+twitch.tv\/([^\/.]+)/,
              match : 1,
              hint: 'Paste Twitch channel url here, example: <b>https://twitch.tv/MyChannel</b>'
            },
            subscribe : {
              validator: /.+twitch.tv\/([^\/.]+)/,
              match : 1,
              hint: 'Paste Twitch channel url here, example: <b>https://twitch.tv/MyChannel</b>'
            },
          },
        },
        steam : {
          types : {
            friend : {
              validator: /.+steamcommunity.com\/profiles\/([^\/.]+)/,
              match : 1,
              hint: 'Paste Twitch profile url here, example: <b>https://steamcommunity.com/profiles/YourProfile</b>'
            },
            subscribe : {
              validator: /.+steamcommunity.com\/profiles\/([^\/.]+)/,
              match : 1,
              hint: 'Paste Twitch profile url here, example: <b>https://steamcommunity.com/profiles/YourProfile</b>'
            },
          },
        },
        tribefire : {
          types : {
            'fill profile' : {
              validator: /.+tribefire-auth.herokuapp.com\/#\/profile/,
              match : null,
              hint: 'Paste Tribefire profile url here, example: <b>https://tribefire-auth.herokuapp.com/#/profile</b>'
            },
            'invite friend' : {
              validator: /.+tribefire-auth.herokuapp.com\/.+\/#\/invite/,
              match : null,
              hint: 'Paste Tribefire contest url here, the validator is <b>tribefire-auth.herokuapp.com/***/#/invite</b>'
            }
          },
        },
      }
    }
  },
  methods: {
    setControls(controlSet = 'default') {
      let Component = this

      let controls
      let defaultControls = [
        /*
        {
          title: 'Update',
          type: 'main',
          action: () => { Component.saveProfile(Component.User) }
        },
        */
      ]
      let contestControls = [
        {
          title: 'Update',
          type: 'main',
          action: () => { Component.saveContest(Component.Contest) }
        },
        {
          title: 'Cancel',
          type: 'alt',
          action: () => { Component.clearContest() }
        }
      ]
      if (controlSet === 'default') {
        controls = controls || defaultControls
      }
      if (controlSet === 'contest') {
        controls = contestControls
      }
      if (controlSet === 'tasks') {
        let taskControls = [
          {
            title: 'Delete',
            type: 'danger',
            action: () => { Component.saveContest(Component.Contest) }
          }
        ]
        taskControls = []
        controls = extend(contestControls,taskControls)
      }

      this.$store.commit('setControls', controls)
    },
    saveContest() {
      info('saveContest()')
      this.$store.dispatch('updateContest', this.Contest).then(response=> {
        this.$toaster.success('Contest saved')
      }).catch(e => {
        this.$toaster.error('Could not save contest')
      })
      this.Contest = null
    },
    clearContest() {
      info('clearContest()')
      this.Contest = null
      this.setControls('default')
    },
    createContest() {
      info('createContest()')

      // this.creating = true
      this.$store.dispatch('createContest', {}).then( date => {
        this.$toaster.success('Contest created')
      }).catch(e => {
        this.$toaster.error('Could not create contest')
      })

    },
    setContest(contest) {
      this.Contest = contest
      this.setControls('contest')
    },
    pickWinner() {

      this.$store.dispatch('pickContestWinner', this.Contest).then( data => {
        info(data)
        this.$forceUpdate()
      }).catch(e => {
        info(e)
      })

    },
    isExpired(contest) {
      let now = new Date().getTime()+''
      let end = contest.end
      info(now,end)
      if (now > end) return true
      return false
    },
    addTask() {
      this.newTask = extend({},this.Task)
      this.newTask.id = this.TaskId
      info(this.newTask)
      this.Contest.tasks.push(this.newTask)
      this.Task = null
    },
    dateFormat(date) {
      // return moment(date).format('MMMM Do YYYY, h:mm:ss a');
      return moment(date).format('Do MMMM');
    },
    validatorHint(task) {

      let social, taskConfig, hint
      try {
        social = this.TasksConfig[task.social]
        taskConfig = social.types[task.type]
        hint = taskConfig.hint
      } catch (e) {
        info('no validator')
      }

      return hint
    },
    validateTaskLink(task) {

      let social, taskConfig, validator
      try {
        social = this.TasksConfig[task.social]
        // info(this.TasksConfig, task.social, task.type, social.types)
        taskConfig = social.types[task.type]
        // info('taskConfig', taskConfig)
        validator = taskConfig.validator
      } catch (e) {
        info('no link validator for', task.social, task.type)
      }
      // info('task.link', task.link)
      // info('validator',validator)
      if (!validator) {
        task.valid = true
        return
      }

      let matches = task.link.match(validator)
      // info('"'+task.link+'"'+'.match('+validator+')', matches)
      if (matches) {
        task.valid = true
        if ( matches[taskConfig.match] ) {
          task.itemId = matches[taskConfig.match]
        } else {
          task.itemId = null
        }
      } else {
        task.valid = false
      }
    }
  },
  computed: {
    ...mapGetters(['User', 'Contests', 'isAdmin', 'isDev', 'debug']),
    restart : {
      get() {
        if (this.Contest && this.Contest.start) {
          return new Date().setTime(this.Contest.start)
        }
      },
      set(date) {
        info('restart.set()',date)
        this.Contest.start = date.getTime()
      }
    },
    reend: {
      get() {
        if (this.Contest && this.Contest.end) {
          return new Date().setTime(this.Contest.end)
        }
      },
      set(date) {
        info('reend.set()',date.getTime())
        this.Contest.end = date.getTime()
      }
    },
    socialsOptions() {
      let socs = _(options.tasks).map( 'social' ).uniq().value()
      info('socs',socs)
      let socials = []
      for (let soc of socs) {
        socials.push( { title : soc, value : soc } )
      }
      info('socials',socials)
      return socials
    },
    tasksOptions() {
      let tsks = _(options.tasks).filter( { social : this.Task.social } )
      let tasks = []
      for (let tsk of tsks) {
        tasks.push( { title : tsk.title, value : tsk.value } )
      }
      info('tasks', tasks)
      return tasks
    },
    TaskId() {
      if (this.Contest) {
        if (this.Task.id) return this.Task.id
        try {
          info('wtf')
          let tsks = _(this.Contest.tasks).map('id').value()
          info('wtf')
          let ids = tsks.sort( (a,b)=> { return parseInt(a) > parseInt(b) ? 1 : - 1 } ).reverse()
          info('ids', ids)
          let id = (ids[0]) + 1
          if (!id) id = 1
          return id
        } catch (e) {
          info('wtf')
          return 0
        }
      }
      info('wtffff')
      // return newTaskId
      return 1
    },
    newTaskId() {
      if (this.Contest) {
        try {
          info('wtf')
          let tsks = _(this.Contest.tasks).map('id').value()
          info('wtf')
          let ids = tsks.sort( (a,b)=> { return parseInt(a) > parseInt(b) ? 1 : - 1 } ).reverse()
          info('ids', ids)
          let id = (ids[0]) + 1
          return id
        } catch (e) {
          info('wtf')
          return 0
        }
      }
      info('wtffff')
      // return newTaskId
      return 1
    }
  },
  mounted() {
    this.$store.dispatch('loadUserContests')
    this.setControls('default')
  }
}
</script>
<template lang="pug" src="./Contests.pug"></template>
<style lang="stylus" src="./Contests.styl" scoped></style>



