<template lang="pug" src="./Profile.pug"></template>
<style lang="stylus" src="./Profile.styl" scoped></style>
<script>
import { info, extend } from '@/kailight/utils'
import { mapGetters } from 'vuex'

import options from '@/config/options'
import tf_input_games from '@/Tribefire/InputGames.vue'
import moment from 'moment'

export default {
  name: 'Profile',
  components: {
    'tf-input-games' : tf_input_games
  },
  data() {
    return {
      ownProfile : true,
      otherUserProfileWarning : false,
      userId : null,
      loading : false
    };
  },
  filters: {
    humanizeTime: function (timestamp) {
      if (parseInt(timestamp) != timestamp) {
        return 'bad format'
      }
      timestamp = parseInt(timestamp)
      let time = new Date(timestamp)
      return moment(time).format("YYYY-MM-DD HH:mm:ss")
    }
  },
  methods: {
    init() {
    info('Profile.init()')

      this.$store.commit('resetUserHistory')

      if (this.$route && this.$route.params && this.$route.params.UserId) {
        this.UserId = this.$route.params.UserId
        this.ownProfile = false
        this.otherUserProfileWarning = true
        this.setControls('admin')
      } else {
        this.loading = false
        this.$store.dispatch('getUserEntries')
        if (this.isAdmin) {
          this.setControls('admin')
        } else {
          this.setControls('user')
        }
      }

    },
    saveProfile() {
    info('saveProfile')

      this.$store.dispatch('updateUser', this.Profile).then( data => {
        info(data)
        this.$toaster.success('Profile saved')
      }).catch( e => {
        info('Some error')
      })
    },
    deleteProfile() {
      this.$store.dispatch('deleteUser', this.Profile).then( (data) => {
        this.$toaster.success('Profile deleted')
        this.$forceUpdate()
      })
      // this.$store.dispatch('updateUser', this.Profile)
    },
    onGameListUpdate(games) {
      this.User.favoriteGames = _(games).map('title')
    },
    setControls(controlSet = null) {
      let Component = this
      let defaultControls = [
        {
          title: 'Update',
          type: 'success',
          action: () => { Component.saveProfile() }
        },
      ]
      let adminControls = [
        {
          title: 'Update',
          type: 'success',
          action: () => { Component.saveProfile() }
        },
        {
          title: 'Delete',
          type: 'danger',
          action: () => { Component.deleteProfile() }
        },
      ]
      let controls = controlSet === 'admin' ? adminControls : defaultControls
      this.$store.commit('setControls', controls)
    },
    logout() {
      info('logout')
      this.$store.dispatch('logout')
    },
    calculateEntries(contest) {
      let entries = 0
      for (let task of contest.tasks) {
        if (['pending','complete'].indexOf(task.status) > -1) {
          entries += parseInt(task.points)
        }
      }
      return entries
    },
    loadUserHistory() {
      this.$store.dispatch('loadUserHistory', this.Profile).then( data => {
        info(data)
        this.$toaster.success('User history updated')
      }).catch( e => {
        info('Some error')
      })
    }
  },
  computed: {
    ...mapGetters(['User','isAdmin','isDev','Entries','Users','UserHistory']),
    options() {
      return options
    },
    Profile() {
      if (this.ownProfile) {
        return extend( {}, this.User )
      }

      if (!this.ownProfile) {
        let OtherUserProfile = null
        if (OtherUserProfile = _(this.Users).find( {_id: this.UserId})) {
          return OtherUserProfile
        }

        this.loading = true
        this.$store.dispatch('loadUser', this.UserId).then( data => {
          info('loaded user', data)
          this.loading = false
          this.$store.dispatch('getUserEntries', data._id )
        }).catch( e => {
          info('failed to load the user', e)
          this.$toaster.error('Failed to load the user')
          this.loading = false
        })

      }

    return {}
    }
  },
  beforeRouteUpdate (to, from, next) {
    info('Profile: beforeRouteUpdate')
    this.init()
  },
  mounted() {
    // document.addEventListener('click', this.onClick);
    info('Profile: mounted')
    // info(this.$route)
    this.init()
  }

}
</script>



