<template lang="pug">
.loginwrapper(v-if="!User.logged").purpleGradient
  .loginboxwrapper(v-if="toggled == 'login'")
    img(src='@/assets/whiteLogoHome.png')
    .bgIll
    .loginbox
      h4.purpleText Login Now
      form(@submit.prevent="")
        input(v-model="wannabeUser.email", placeholder="Email")
        input(type="password" v-model="wannabeUser.password", placeholder="Password")
        button.purpleButton(@click="login") Enter
      .errorMessage(:class="loginError ? 'visible' : 'invisible'") {{loginErrorMessage}}
    a(@click="toggle('signup')") Sign Up, Baby

  .loginboxwrapper(v-if="toggled == 'signup'")
    img(src='@/assets/whiteLogoHome.png')
    .bgIll
    .loginbox
      h4.purpleText Register now
      form(@submit.prevent="")
        input(v-model="wannabeUser.email", placeholder="Email")
        input(type="password" v-model="wannabeUser.password", placeholder="Password")
        button.purpleButton(@click="register") Join Us
      .errorMessage(:class="signupError ? 'visible' : 'invisible'") {{signupErrorMessage}}
    a(@click="toggle('login')") LogIn instead
    br
    a(@click="toggle('clientSignup')") Become a Tribefire Pro

  .loginboxwrapper(v-if="toggled == 'clientSignup'")
    img(src='@/assets/whiteLogoHome.png')
    .bgIll
    .loginbox
      h4.purpleText Signup for Pro Account
      form(@submit.prevent="")
        input(v-model="wannabeUser.email", placeholder="Email")
        input(type="password" v-model="wannabeUser.password", placeholder="Password")
        <br>
        p If you want to apply to Tribefire Pro account you need to accept the <a href="#" style="color: inherit">ToS</a>
        span I accept the ToS
        input(type="checkbox" v-model="wannabeUser.clientAgreement")
        button.purpleButton(@click="registerAsClient") Join Us
      .errorMessage(:class="signupError ? 'visible' : 'invisible'") {{signupErrorMessage}}
    a(@click="toggle('signup')") Oh, nevermind

</template>

<script>
import { info, _ } from '@/kailight/utils'
import { mapGetters } from 'vuex'

export default {
  name: 'TribefireLoginSignup',
  data () {
    return {
      wannabeUser : {
        login : null,
        email : null,
        clientAgreement : null,
        password : null,
        password1 : null,
        password2 : null
      },
      toggled : 'login',
      loginError : false,
      loginErrorMessage : "Can't let you in :(",
      signupErrorMessage : "Can't sign you up :(",
      emailIsValid : false,
      signupError : false,
      signupErrorMessage : null
    }
  },
  methods: {
    regErr(msg=null) {
      let COMP = this
      if (msg) {
        this.signupError = true
        this.signupErrorMessage = msg
        setTimeout( function() { COMP.regErr() }, 2000)
      } else {
        this.signupError = false
        this.signupErrorMessage = null
      }
    },
    toggle(regtype) {
      this.toggled = regtype
    },
    logErr(msg=null) {
      if (msg) {
        this.loginError = true
        this.loginErrorMessage = msg
      } else {
        this.loginError = false
        this.loginErrorMessage = null
      }
      setTimeout( () => this.logErr(), 2000)
    },
    passwordsMatch() {
      if (this.wannabeUser.password1 === this.wannabeUser.password2) {
        return true
      }
      return false
    },
    validateEmail() {
      if (this.wannabeUser.email && this.wannabeUser.email.match(/.+?@.+?\..+?/)) {
        this.emailIsValid = true
      } else {
        this.emailIsValid = false
      }
      info('email validated as', this.emailIsValid)
      return this.emailIsValid
    },
    validateAgreement() {
      if (!this.wannabeUser.clientAgreement)
        return false

      return true
    },
    login() {
      let COMP = this

      if (!this.wannabeUser.email) {
        return this.logErr('No email')
      }
      if (!this.wannabeUser.password) {
        return this.logErr('No password')
      }

      COMP.$store.dispatch('login', { email : this.wannabeUser.email, password : this.wannabeUser.password } )
      .then( response => {
        info(response)
      }).catch(e => {
        COMP.loginError = true
        setTimeout( () => { COMP.loginError = false }, 2000 )
      })
      
    },
    register() {
      let COMP = this

      if (!this.validateEmail()) {
        // this.$toaster.error("The email doesn't look valid")
        // this.registrationErrorMessage = "This email address doesn't look valid"
        this.regErr("This email address doesn't look valid")
        return
      }

      if (this.wannabeUser.email && this.wannabeUser.password) {
        COMP.$store.dispatch('register', { email : this.wannabeUser.email, password : this.wannabeUser.password } )
        .then( response => {
          this.regErr()
        }).catch( e => {
          COMP.signupError = true
          info(e.status)
          if (e.status == 409) {
            this.regErr('This email is already taken.')
            return
          } else {
            this.regErr("Could not register you right now.")
            return
          }
          setTimeout( () => { COMP.signupError = false }, 2000 )
        })
      }
    },
    registerAsClient() {
      let COMP = this

      if (!this.validateEmail()) {
        this.regErr("This email address doesn't look valid")
        return
      }

      if (!this.validateAgreement()) {
        this.regErr("You need to agree to our terms")
        return
      }

      if (this.wannabeUser.email && this.wannabeUser.password) {
        COMP.$store.dispatch('register', { role : 'pro', email : this.wannabeUser.email, password : this.wannabeUser.password } )
          .then( response => {
            info('registered as pro')
            // this.regErr()
          }).catch( e => {
            info(e, e.status, e.status === 409)
            if (e.status == 409) {
              COMP.regErr("This email is already taken.")
              return
            } else {
              COMP.regErr("Could not register you right now.")
              return
            }
            setTimeout( () => { COMP.signupError = false }, 2000 )
        })
      }
    }
  },
  computed: {
    ...mapGetters(['User'])
  }
}
</script>


<style lang="stylus" scoped>
.purpleGradient
  background: radial-gradient(#bc4e9c, #f80759);
.bgIll
  position absolute
  width 100%
  height 100%
  // background-image url("../assets/bgillustration.png")
  background-image url("/bgillustration.png")
  background-blend-mode: multiply
  background-size cover
  opacity .1
  z-index -1
.loginwrapper
  width 100%
  display: flex
  flex-direction: row
  flex-wrap: nowrap
  justify-content: center
  align-content: stretch
  align-items: center
  position relative
  height 100vh
  // min-height 50rem
  .loginboxwrapper
    display flex
    z-index 1
    flex-direction column
    justify-content center
    align-items center
    align-self: auto;
    a
      color white
      margin-top 1rem
      cursor pointer
    img
      margin-bottom 2rem
      z-index 1
    h2
      font-weight 400
      text-align: center
      color #f63
    .loginbox
      border-radius .2rem
      box-shadow 0px 2px 4px rgba(0,0,0,.7)
      background white
      padding 2rem
      display flex
      flex-direction column
      justify-content center
      align-items center
      h4
        margin-bottom 1rem
      form
        text-align center
        display flex
        justify-content center
        align-items center
        flex-direction column
    label
      display block
      width: 100%
      text-align: right;
      font-family bungee;
    input
      margin 5px 0
      outline none
      border none
      border-radius 2px;
      box-shadow 0 0px 2px rgba(0,0,0,.5)
      padding 5px 10px
    button
      margin-top 1rem
      cursor pointer
      font-family "Bungee"
      font-weight normal
.errorMessage
  height 1rem
  padding 0.2rem
  transition 1s
  color red
  font-weight bold
  margin-top 0.2rem
  &.visible
    opacity 1
  &.invisible
    opacity 0
</style>



