<template lang="pug">
div(:class="classes")
  code(v-if="debug") {{classes}} {{width}} {{height}}
  slot
</template>

<style lang="stylus">
#app
  display flex
  align-items stretch
  &.portrait
    flex-direction column
  &.landscape
    flex-direction row
</style>

<script>
import { info } from '@/kailight/utils'

export default {
  name: 'TribefireWrapper',
  data() {
    return {
      width : null,
      height : null
    }
  },
  props: {
    debug : Boolean
  },
  methods: {
    calc(win) {
      if (win.innerWidth && win.innerHeight) {
        this.width = win.innerWidth
        this.height = win.innerHeight
      }
    }
  },
  mounted() {
    let Comp = this
    window.addEventListener('load', function (ev) {
      Comp.calc(ev.currentTarget)
      // info(ev)
    })
    window.addEventListener('resize', function (ev) {
      Comp.calc(ev.currentTarget)
      // info(ev)
    })
    // console.info('TribefireWrapper mounted')
  },
  computed: {
    classes() {
      // info('classes')
      let classes = []
      if (this.width > this.height * 1.3) {
        classes.push('landscape')
        if (this.width > 1366) {
          classes.push('md')
        }
        else if (this.width > 1800) {
          classes.push('lg')
        } else {
          classes.push('sm')
        }
      } else {
        classes.push('portrait')
        if (this.height > 600) {
          classes.push('md')
        }
        else if (this.height > 1000) {
          classes.push('lg')
        } else {
          classes.push('sm')
        }
      }
      // info(classes)
    return classes
    }
  }
}
</script>



